/*---
title: Typography
section: Elements
---*/

:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --font-size: 100%;
  --font-size--tiny: .125rem;
  --font-size--semi-tiny: .25rem;
  --font-size--small: .5rem;
  --font-size--semi-small: .75rem;
  --font-size--normal: 1rem;
  --font-size--normal-plus: 1.25rem;
  --font-size--semi-medium: 1.5rem;
  --font-size--medium: 2rem;
  --font-size--semi-large: 2.5rem;
  --font-size--large: 3rem;
  --font-line-height: 1.5;
  --font-weight: 400;
  --font-weight--bold: 800;
  --heading-font-size: 1.25rem;
}

@custom-selector :--headings h1, h2, h3, h4, h5, h6;

:root {
  /* stylelint-disable root-no-standard-properties */
  color: var(--body-copy);
  font: var(--font-size) / var(--font-line-height) var(--font-family);
  font-weight: var(--font-weight);
  line-height: 1;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%; /* iOS 8+ */
  /* stylelint-enable */
}

::selection {
  background-color: var(--highlight-warning);
  color: var(--body-copy);
  text-shadow: none;
}

:--headings {
  font-size: var(--heading-font-size);
  font-weight: inherit;
  line-height: 1.3;
  margin: 0 0 8px;
}

code,
kbd,
pre,
samp { font-family: monospace, monospace; }

hr {
  border-top: 2px solid var(--grey-300);
  margin: 1em 0;
}

a[href]:not([class]) {
  @mixin link-hover;
  color: var(--link-color);
  text-decoration: none;
}

p {
  line-height: 1.4;
  margin-bottom: 1em;
}

.is-featured p:first-of-type {
  font-size: var(--font-size--semi-medium); 
}

small {
  display: block;
  font-size: 75%;
  line-height: 1.5;
  margin-bottom: 1em;
}

figure {
  display: inline-block;
  float: left;
  margin: 0 1rem 1em 0;
  overflow: hidden;
  text-align: center;
}

figcaption {
  font-size: var(--font-size--semi-small);
  padding: .5em 0;
}
