/*---
title: Label
section: Components
---*/

@import 'stylep-badge';

:root {
  --label-transparent: transparent;
  --label-grey: var(--grey-500);
  --label-blue: var(--blue-400);
  --label-orange: var(--orange-500);
  --label-green: var(--green-600);
  --label-red: var(--red-600);
  --label-purple: var(--magenta-500);
  --label-magenta: var(--label-purple);
  --label-orange: var(--orange-500);
  --label-gold: var(--gold-200);
}

@define-mixin label $badge-color: grey {
  @mixin badge-solid var(--label-$(badge-color)), contrast(var(--label-$(badge-color))), 2px;
}

.c-label {
  @mixin badge-inline;
  @mixin label transparent;
  color: var(--body-copy);
  font-weight: bold;
  padding: 3px 6px;
  text-transform: uppercase;
  user-select: none;

  .c-icon {
    fill: var(--white);
    height: 15px;
    margin-top: -3px;
    width: 15px;
  }
}

.c-label--positive { @mixin label green; }

.c-label--informative { @mixin label blue; }

.c-label--informative-alt { @mixin label magenta; }

.c-label--neutral { @mixin label grey; }

.c-label--live { @mixin label gold; }

.c-label--alterable { @mixin label orange; }

.c-label--negative { @mixin label red; }
