/*---
title: Toolbar
section: Objects
---*/

:root {
  --brand-logo: 'img/navis-logo--inverted.svg';
  --toolbar-height: calc(56px + 1em);
}

.o-toolbar {
  @mixin middle;
  @mixin row;
  background-color: var(--primary-color);
  color: var(--grey-500);
  height: 56px;
  padding: 0 .5rem;
  @media (--md-up) {
    border-color: var(--bar-border-color);
    border-style: solid;
    border-width: 0 0 1px;
  }

  & .c-button--icon {
    @mixin responsive-link-colors var(--white), var(--white), var(--nav-active-color);
  }

  & .c-icon { color: var(--white); }

  & * { margin-bottom: 0 !important; }
}

.o-toolbar__nav {
  @mixin column-pattern;
  @mixin middle;
  display: flex;
  flex-wrap: wrap;
  padding: 0;

  @media (--md-up) {
    &:nth-child(even) { @mixin end; }

    &:nth-child(odd) { @mixin start; }
  }
}

.o-toolbar__brand {
  @mixin hide-text;
  /* stylelint-disable function-url-quotes */
  background-image: url(var(--brand-logo));
  /* stylelint-enable */
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 48px;
  margin: 6px 0 0 !important;
  position: relative;
  width: 80px;
  @media (--md-up) {
    width: 90px;
  }
}

.o-toolbar__brand,
.o-toolbar__product {
  display: inline-block;
  color: var(--white);
  text-transform: uppercase;
}

.o-toolbar__link {
  display: inline-block;
  padding: 0 10px;
  position: relative;
  vertical-align: middle;
}

.o-toolbar--small {
  border-bottom-width: 3px !important;
  height: 44px;
}

.o-toolbar--secondary {
  border: none !important;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  @media (--md-up) {
    background-color: var(--white);
    box-shadow: 0 0 3px rgba(0, 0, 0, .36);
    color: var(--body-copy);
    margin: 0 0 1em;
    position: relative;
    z-index: 20;
  }
}

@media (--sm-down) {
  .o-toolbar__view {
    @mixin column 6;
    h1 {
      display: inline-block;
      font-size: var(--font-size--semi-medium);
      font-weight: normal;
      margin: 0;
      vertical-align: middle;
      white-space: nowrap;
    }
  }

  .o-toolbar__action {
    display: inline-block;
    padding: 1em;
    margin: 0;
  }

  .o-toolbar__actions {
    @mixin column 6;
    @mixin end;
    > div,
    > ul,
    > li {
      display: inline-block;
    }
  }
}

@media (--md-up) {
  .o-toolbar--secondary { width: 100%; }
}

/* TODO Find a way to get rid of this */
.o-toolbar__mnav {
  width: 35%;
  @media (--md-up) { width: 50%; }

  button { cursor: pointer; }
}

.o-toolbar__mitem button {
  padding: 1em .3rem;
  transition: background-color var(--global-duration) ease-in-out;
  @media (--md-up) { padding: 1em .5rem; }

  &:focus { outline: none; }

  @mixin active {
    background-color: var(--nav-active-color);
    color: var(--nav-active-copy);
  }
}
